import { request } from 'utils';

const prefix = '/api/finance';

export const getAccountsReceivable = ({
  label,
  isFee,
  isTrainExpense,
  isRestudy,
  isRetest,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/Receivable',
    method: 'GET',
    params: {
      label,
      isFee,
      isTrainExpense,
      isRestudy,
      isRetest,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getAccountsReceivableById = id =>
  request({
    prefix,
    url: `/Receivable/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const createAccountReceivale = async values => {
  await request({
    prefix,
    url: `/Receivable`,
    method: 'POST',
    params: {},
    data: { ...values },
  });
};

export const updateAccountReceivable = async values => {
  const { id } = values;
  await request({
    prefix,
    url: `/Receivable/${id}`,
    method: 'PUT',
    params: {},
    data: { ...values },
  });
};

export const deleteAccountReceivable = id =>
  request({
    prefix,
    url: `/Receivable/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



