import React, { Component } from 'react';
import { TableDataEditCell, InputMoney } from 'components/common';
import { moneyToString } from 'utils/formatUtil';
import { Form } from 'antd';

export default class TableSubjectData extends Component {

  columns = [
    {
      title: 'Ký hiệu',
      dataIndex: 'studyFieldId',
      key: 'studyFieldId',
    },
    {
      title: 'Môn',
      dataIndex: 'subjectLabel',
      key: 'subjectLabel',
    },
    {
      title: 'Số tín chỉ',
      dataIndex: 'noOfCredit',
      key: 'noOfCredit',
    },
    {
      title: 'Tên lớp học lại',
      dataIndex: 'classCreditName',
      key: 'classCreditName',
    },
    {
      title: 'Số SV',
      dataIndex: 'noOfStudent',
      key: 'noOfStudent',
    },
    {
      title: 'Đơn giá',
      dataIndex: 'unitPrice',
      key: 'unitPrice',
      editable: true,
      render: text => moneyToString(text),
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.index}.unitPrice`, {
            initialValue: record.dongia,
          })(
            <InputMoney
              onPressEnter={onSave}
              onBlur={onSave}
              refInput={setRefFormItem}
              style={{ minWidth: '200px' }}
            />
          )}
        </Form.Item>
      ),
    },
    {
      title: 'Thành tiền',
      dataIndex: 'totalPrice',
      key: 'totalPrice',
      render: (text, record) => {
        let resultShow;
        if (this.props.paramSearch.collectTypeId === "1") {
          resultShow = moneyToString(record.unitPrice * record.noOfCredit);
        } else {
          resultShow = moneyToString(record.unitPrice);
        }
        return resultShow;
      },
    },
  ];

  handleSaveCellData = newSubject => {
    this.props.handleUpdateDonGia(newSubject);
    return newSubject;
  };

  render() {
    const { service, rowSelection, paramSearch, reload } = this.props;
    console.log("paramSearch");
    console.log(paramSearch);
    return (
      <TableDataEditCell
        rowSelection={rowSelection}
        reload={reload}
        paramSearch={paramSearch}
        columns={this.columns}
        service={service}
        handleSaveCellData={this.handleSaveCellData}
        allowGetData={
          paramSearch &&
          !!paramSearch.trainTypeId &&
          !!paramSearch.facultyId &&
          !!paramSearch.courseId &&
          !!paramSearch.yearEduId &&
          !!paramSearch.semesterId &&
          !!paramSearch.receivableId &&
          !!paramSearch.unitPrice &&
          !!paramSearch.collectTypeId
        }
      // rowKey="id"
      />
    );
  }
}



