import React, { Component } from 'react';
import { Row, Col, Form, Radio, InputNumber } from 'antd';
import { moneyToString, stringToMoney } from 'utils/formatUtil';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { SelectAjax } from 'components/common';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { getAccountsReceivable, getAccountsReceivableById } from 'services/accountsReceivable';

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  getDotthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getKhoanthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Khoản thu 1',
        },
        {
          id: '2',
          label: 'Khoản thu 2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getDot = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };

  handleOnchangeReceivableId = async (id) => {
    if (id) {
      const { setFieldsValue } = this.props.form;
      const result = await getAccountsReceivableById(id);
      setFieldsValue({
        unitPrice: result.data.amount,
      });
    }
  }


  formItemLayout = 6;

  render() {
    const { getFieldDecorator, setFieldsValue } = this.props.form;
    const { onChangeMasterPrice, masterPrice } = this.props;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <YearSemesterInput
            form={this.props.form}
            optionFieldDecorator={optionFieldDecorator}
            span={5}
          />
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Khoản thu">
              {getFieldDecorator('receivableId')(
                <SelectAjax
                  typeValue="int"
                  service={getAccountsReceivable}
                  setFieldsValue={setFieldsValue}
                  onChange={this.handleOnchangeReceivableId}
                  params={{ isRestudy: true, isRetest: true }}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Đợt thu">
              {getFieldDecorator('paymentTime')(
                <SelectAjax service={this.getDotthu} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Nhập đơn giá môn">
              {getFieldDecorator('unitPrice')(
                <InputNumber
                  defaultValue={masterPrice}
                  formatter={value => moneyToString(value)}
                  parser={value => stringToMoney(value)}
                  onChange={onChangeMasterPrice}
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <b style={{ color: 'red' }}>Lưu ý: Dữ liệu học lại, thi lại lấy từ phòng đào tạo</b>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('collectTypeId')(
                <Radio.Group setFieldsValue={setFieldsValue}>
                  <Radio value="1">Lập DS học lại</Radio>
                  <Radio value="2">Lập DS thi lại</Radio>
                  <Radio value="3">Lập DS thi lại cải thiện</Radio>
                </Radio.Group>
              )}
            </Form.Item>
          </Col>
          <Col span={6} style={{ float: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" title="Tìm kiếm" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



