import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Btn, BtnAddNew, BtnDelete, BtnExport } from 'components/common/button';
import { getStudents } from 'services/reTuitionReceipts';
import {
  getRestudySummaryForFinance,
} from 'services/classCreditEnrollment';
import {
  exportExcelRestudyCreditReceivable,
  exportRestudyCreditReceivable,
  getRestudyCreditReceivable,
  createCreditReceivable,
  deleteCreditReceivable,
} from 'services/creditReceivable';
import { getQueryParams } from 'utils/urlUtil';
import { Card, Row, Icon } from 'antd';
import TableSubjectData from './TableSubjectData';
import TableStudentData from './TableStudentData';
import SearchBar from './SearchBar';
// import './style.css';

class ReTuitionReceipts extends Component {
  state = {
    paramSearch: {},
    subjectSelected: {},
    studentSelected: {},
    masterPrice: '',
    reload: 0,
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  onChangeMasterPrice = value => {
    this.setState({
      masterPrice: value,
    });
  };

  getStudentsBySubject = index =>
    getStudents(index).map(student => this.updateStudentKey(student, index));

  addStudents = async () => {
    const { subjectSelected, paramSearch, reload } = this.state;
    const data = { ...subjectSelected };
    data.paymentTime = paramSearch.paymentTime;
    data.receivableId = paramSearch.receivableId;
    data.collectTypeId = paramSearch.collectTypeId;
    await createCreditReceivable(data);
    this.setState({
      reload: reload + 1,
    });
  };

  removeStudents = async () => {
    const { studentSelected, reload } = this.state;
    await deleteCreditReceivable(studentSelected);
    this.setState({
      reload: reload + 1,
    });
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleUpdateDonGia = newSubject => {
    this.setState({
      subjectSelected: { ...newSubject },
    });
  };

  exportExcel = async () => {
    const { paramSearch } = this.state;
    const response = await exportExcelRestudyCreditReceivable(paramSearch);
    console.log(response);
  }

  printPDF = async () => {
    const { paramSearch } = this.state;
    const response = await exportRestudyCreditReceivable(paramSearch);
    console.log(response);
  }

  enableAddBtn = () =>
    this.state.subjectSelected && Object.values(this.state.subjectSelected).length > 0;
  enableDeleteBtn = () =>
    this.state.studentSelected.length > 0;
  // enablePrintAndExport = () => this.state.students.length > 0;

  render() {
    const {
      // studentSelectedRowKeys,
      reload
    } = this.state;
    const studentSelection = {
      // selectedRowKeys: studentSelectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const studentSelected = [];

        selectedRows.forEach(selectedRow => {
          studentSelected.push(selectedRow.id);
        });

        this.setState({
          studentSelected,
        });
      },
    };

    const subjectSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const subjectSelected = { ...selectedRows[0] };

        // selectedRows.forEach(selectedRow => {
        //   subjectSelected[selectedRowKeys] = selectedRow;
        // });

        this.setState({
          subjectSelected,
        });
      },
    };

    return (
      <Card>
        <Row>
          <SearchBar
            masterPrice={this.state.masterPrice}
            onChangeMasterPrice={this.onChangeMasterPrice}
          />
        </Row>
        <Row>
          <TableSubjectData
            service={getRestudySummaryForFinance}
            rowSelection={subjectSelection}
            paramSearch={this.state.paramSearch}
            handleUpdateDonGia={this.handleUpdateDonGia}
            reload={reload}
          />
        </Row>
        <Row id="btns-action">
          <BtnAddNew
            title="Thêm SV khoản thu"
            onClick={this.addStudents}
            disabled={!this.enableAddBtn()}
          />
          <BtnDelete
            title="Xóa SV khoản thu"
            onClick={this.removeStudents}
            disabled={!this.enableDeleteBtn()}
          />
          <Btn
            // disabled={!this.enablePrintAndExport()}
            onClick={this.printPDF}
          >
            <Icon type="printer" />
            In
          </Btn>
          <BtnExport
            // disabled={!this.enablePrintAndExport()}
            onClick={this.exportExcel}
          />
        </Row>
        <Row>
          <TableStudentData
            service={getRestudyCreditReceivable}
            paramSearch={this.state.paramSearch}
            rowSelection={studentSelection}
            reload={reload}
          />
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReTuitionReceipts {...props} />
  </Page>
);



