import { request, requestDownload } from 'utils';

const prefix = '/api/finance';

export const getRestudyCreditReceivable = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId,
  receivableId,
  paymentTime,
  collectTypeId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) => {
 return request({
    prefix,
    url: `/CreditReceivable/GetRestudyCreditReceivable`,
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      yearEduId,
      semesterId,
      receivableId,
      paymentTime,
      collectTypeId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });
};

export const createCreditReceivable = ({
  ccId,
  classCreditName,
  noOfCredit,
  theoryPeriod,
  practicePeriod,
  exercisePeriod,
  groupPeriod,
  selfPeriod,
  subjectId,
  subjectLabel,
  unitPrice,
  yearEduId,
  semesterId,
  registerSessionId,
  paymentTime,
  receivableId,
  collectTypeId
}) => {
 return request({
    prefix,
    url: `/CreditReceivable/CreateCreditReceivable`,
    method: 'POST',
    params: {},
    data: {
      ccId,
      classCreditName,
      noOfCredit,
      theoryPeriod,
      practicePeriod,
      exercisePeriod,
      groupPeriod,
      selfPeriod,
      subjectId,
      subjectLabel,
      unitPrice,
      yearEduId,
      semesterId,
      registerSessionId,
      paymentTime,
      receivableId,
      collectTypeId
    },
  });
};

export const exportExcelRestudyCreditReceivable = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId,
  receivableId,
  paymentTime,
  collectTypeId,
}) =>
  requestDownload({
    prefix,
    defaultFileName: 'RestudyCreditReceivable.xlsx',
    url: '/CreditReceivable/ExportExcelRestudyCreditReceivable',
    method: 'POST',
    params: {},
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      yearEduId,
      semesterId,
      receivableId,
      paymentTime,
      collectTypeId,
    },
  });

export const exportRestudyCreditReceivable = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId,
  receivableId,
  paymentTime,
  collectTypeId,
}) =>
  requestDownload({
    prefix,
    defaultFileName: `RestudyCreditReceivable.pdf`,
    url: `/CreditReceivable/ExportRestudyCreditReceivable`,
    params: {},
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      yearEduId,
      semesterId,
      receivableId,
      paymentTime,
      collectTypeId,
    },
    method: 'POST',
  });

export const deleteCreditReceivable = (ids) => {
  return request({
    prefix,
    url: `/CreditReceivable/DeleteCreditReceivable`,
    method: 'PUT',
    params: {},
    data: { ids },
  });
};



