import React, { Component } from 'react';
import { TableData } from 'components/common';
import { moneyToString } from 'utils/formatUtil';
import moment from 'moment';

export default class TableStudentData extends Component {
  state = {};

  columns = [
    {
      title: 'Mã SV',
      dataIndex: 'studentCode',
      key: 'studentCode',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Giới tính',
      dataIndex: 'genderText',
      key: 'genderText',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'classname',
      key: 'classname',
    },
    {
      title: 'Số tiền phải nộp',
      dataIndex: 'totalAmt',
      key: 'totalAmt',
      render: text => moneyToString(text),
    },
    {
      title: '% MG',
      dataIndex: 'reducedPercent',
      key: 'reducedPercent',
    },
    {
      title: 'Số tiền miễn giảm',
      dataIndex: 'reducedAmt',
      key: 'reducedAmt',
      render: text => moneyToString(text),
    },
    {
      title: 'Số tiền còn nộp',
      dataIndex: 'remainAmt',
      key: 'remainAmt',
      render: text => moneyToString(text),
    },
    {
      title: 'Số tiền đã nộp',
      dataIndex: 'paidAmt',
      key: 'paidAmt',
      render: text => moneyToString(text),
    },
    {
      title: 'Ghi chú',
      dataIndex: 'note',
      key: 'note',
    },
    {
      title: 'Ngày cập nhật',
      dataIndex: 'updatedDate',
      key: 'updatedDate',
      render: date => <span>{ date ? moment(date).format("DD-MM-YYYY") : '' }</span>,
    },
  ];

  render() {
    const {
      service,
      paramSearch,
      reload,
      rowSelection
    } = this.props;
    return (
      <TableData
        reload={reload}
        rowSelection={rowSelection}
        service={service}
        paramSearch={paramSearch}
        columns={this.columns}
        allowGetData={
          paramSearch &&
          !!paramSearch.trainTypeId &&
          !!paramSearch.facultyId &&
          !!paramSearch.courseId
        }
      />
    );
  }
}



